
    .center {
        width: 90%;
        height: 75%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 50px;
        margin: auto;
        text-align: center;
    }

    .headLine1, .headLine2 {
        font-family: Georgia,serif;
        color: #7e7e7e;
        font-variant: small-caps; 
        text-transform: none;
        font-weight: 100;
        margin-bottom: 0;
    }

    .headLine1{
        font-size: 2.5em;
    }
    .headLine2{
        font-size: 1.5em;
    }
    .paragraph {
        font-family: 'Helvetica Neue', 'Lucida Grande', Helvetica, Arial, Verdana, sans-serif;
        margin-top: 1em;
        color: #d3d3d3;
        font-size: 1em;
    }